import * as React from "react";

type Action =
  | {
      type: "player 1 clicked";
    }
  | {
      type: "player 2 clicked";
    }
  | {
      type: "win range changed";
      payload: number;
    }
  | {
      type: "reset clicked";
    };

type State = {
  p1: number;
  p2: number;
  win: number;
};

const initialState: State = {
  p1: 0,
  p2: 0,
  win: 1
};

export function Hello() {
  const [{ p1, p2, win }, dispatch] = React.useReducer(
    (state: State, action: Action) => {
      const gameOver = state.p1 === state.win || state.p2 === state.win;
      switch (action.type) {
        case "player 1 clicked": {
          return {
            ...state,
            p1: gameOver ? state.p1 : state.p1 + 1
          };
        }
        case "player 2 clicked": {
          return {
            ...state,
            p2: gameOver ? state.p2 : state.p2 + 1
          };
        }
        case "win range changed": {
          return {
            ...state,
            win: action.payload
          };
        }
        case "reset clicked": {
          return initialState;
        }
      }
    },
    initialState
  );
  return (
    <div>
      <h1>
        <span style={{ color: p1 === win ? "green" : "inherit" }}>{p1}</span>
        {` vs `}
        <span style={{ color: p2 === win ? "green" : "inherit" }}>{p2}</span>
      </h1>
      <p>Score Limit = {win}</p>
      <input
        type="range"
        min={1}
        max={10}
        step={1}
        value={win}
        onChange={e => {
          void dispatch({
            type: "win range changed",
            payload: parseInt(e.target.value)
          });
        }}
      />
      <button onClick={() => void dispatch({ type: "player 1 clicked" })}>
        Player 1
      </button>
      <button onClick={() => void dispatch({ type: "player 2 clicked" })}>
        Player 2
      </button>
      <button onClick={() => void dispatch({ type: "reset clicked" })}>
        Reset
      </button>
    </div>
  );
}
